body,
html,
#root {
  font-size: 20px;
  color: #666;
  margin: 0;
  background: #f8f8f8;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 1;
      flex-grow: 1;
  min-height: 100%;
