.pagination button {
  position: absolute;
  background: none;
  border: none;
  cursor: pointer;
  font-family: inherit;
  font-size: 1em;
  opacity: 1;
  -webkit-transition: opacity 300ms ease;
  -o-transition: opacity 300ms ease;
  transition: opacity 300ms ease;
}

.pagination button:hover {
  opacity: 0.6;
}

.pagination .decrement {
  right: calc(50% + 16px);
}

.pagination .increment {
  left: calc(50% + 16px);
}

@-webkit-keyframes appear {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes appear {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@-webkit-keyframes appearUp {
  from {
    opacity: 0.5;
    -webkit-transform: translate3d(0, 10%, 0);
            transform: translate3d(0, 10%, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
  }
}

@keyframes appearUp {
  from {
    opacity: 0.5;
    -webkit-transform: translate3d(0, 10%, 0);
            transform: translate3d(0, 10%, 0);
  }

  to {
